<template>
  <div class="variant-item">
    <el-form :model="formInline">
      <el-row :gutter="20">
        <el-col :span="1">
          <b>{{ formInline.id }}) </b>
        </el-col>
        <el-col :span="17">
          <el-input v-model="formInline.text" size="small" @change="change"></el-input>
        </el-col>
        <el-col :span="6">
          <el-radio-group v-model="formInline.isCorrect" size="small" @change="change">
            <el-radio-button label="true">Правильный</el-radio-button>
            <el-radio-button label="false">Нет</el-radio-button>
          </el-radio-group></el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
export default {
    data() {
        return {
            formInline: {
                id: this.id,
                text: '',
                isCorrect: false,
            }
        }
    },
    methods: {
        change() {
            this.$emit('change-variant', this.id, this.formInline.text, this.formInline.isCorrect);
        }
    },
    props: {
        id: String
    }
}
</script>

<style scoped lang="scss">
  .variant-item {
    margin-bottom: 10px;
  }
</style>
