<template>
    <div class="home">
        <el-row>
            <el-col :span="12">
                <div class="grid-content bg-purple">
                    <el-divider content-position="left"><b>Вопрос</b></el-divider>
                    <el-form ref="form" :model="form" label-width="120px">
                        <el-form-item label="ID">
                            <el-input v-model="form.id"></el-input>
                        </el-form-item>
                        <el-form-item label="Вопрос">
                            <el-input v-model="form.text" type="textarea" :rows="7"></el-input>
                        </el-form-item>
                        <el-form-item label="Теги">
                            <el-select
                                    v-model="form.tags"
                                    multiple
                                    filterable
                                    allow-create
                                    default-first-option
                                    placeholder="Выбрать теги"
                                    class="tags-select"
                            >
                                <el-option
                                        v-for="item in tags"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="Подсказка">
                            <el-input v-model="form.hint" type="textarea" :rows="3"></el-input>
                        </el-form-item>
                        <el-form-item label="Документация">
                            <el-input v-model="form.hintUrl"></el-input>
                        </el-form-item>
                        <el-divider content-position="left"><b>Варианты ответов</b></el-divider>
                        <div class="answers">
                            <VariantAnswer
                                    v-for="item in form.variants"
                                    :key="item.id"
                                    :id="item.id"
                                    v-on:change-variant="changeVariant"
                            />
                        </div>

                        <el-form-item>
                            <el-button type="primary" @click="copyJson" class="btn" :data-clipboard-text="questionString">Copy JSON</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
            <el-col :span="12" >
                <el-divider content-position="left"><b>JSON для просмотра</b></el-divider>
                <div class="json-item">
                    <vue-json-pretty
                            :data="question"
                            :showLine="true"
                            :showDoubleQuotes="true"
                            :showLength="true"
                            :highlightMouseoverNode="false"
                    >
                    </vue-json-pretty>
                </div>
                <el-divider content-position="left"><b>JSON для копирования</b></el-divider>
                <div class="json-item">
                    <pre>{{question}}</pre>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import VueJsonPretty from 'vue-json-pretty';
    import ClipboardJS from 'clipboard';
    import VariantAnswer from '../components/VariantAnswer';
    import TagOptions from '../data/options-tags';

    export default {
        name: 'home',
        data() {
            return {
                form: {
                    id: '',
                    text: '',
                    variants: [
                        {
                            id: 'A',
                            text: '',
                            isCorrect: false,
                        },
                        {
                            id: 'B',
                            text: '',
                            isCorrect: false,
                        },
                        {
                            id: 'C',
                            text: '',
                            isCorrect: false,
                        },
                        {
                            id: 'D',
                            text: '',
                            isCorrect: false,
                        },
                    ],
                    tags: [],
                    hint: '',
                    hintUrl: '',
                },
                tags: TagOptions
            }
        },
        computed: {
            question: function () {
                // `this` указывает на экземпляр vm
                let question = Object.assign({}, this.form);

                question.text = question.text.split(/\r?\n/).join('\n');

                return question;
            },
            questionString: function () {
                return JSON.stringify(this.question);
            }
        },
        methods: {
            copyJson() {
                new ClipboardJS('.btn');
            },
            changeVariant(id, text, isCorrect) {
                var variants = this.form.variants;
                variants.forEach(function (variant, index) {
                    console.log(variant);
                    if (variant.id === id) {
                        variants[index].text = text;
                        variants[index].isCorrect = isCorrect === 'true';
                    }
                });

                this.form.variants = variants;
            }
        },
        components: {
            VueJsonPretty,
            VariantAnswer,
            ClipboardJS
        },
    }
</script>

<style scoped>
    .json-item {
        text-align: left;
        margin: 0 15px;
        border: 1px solid #DCDFE6;
        padding: 25px;
        border-radius: 5px;
    }
    .answers {
        text-align: left;
    }
    .tags-select {
        width: 100%;
    }
</style>